#battery-section{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  }
.mobile-container#battery-section{
  background: none;
  }
.mobile-container#battery-section::before{
  content: "";
  position: absolute;
  width: 240px;
  height: 240px;
  background: linear-gradient(#f3f5f5,#9c27b0);
  border-radius: 50%;
  transform: translate(-150px, -100px);
  }
.mobile-container#battery-section{
  height: 450px;
  }
.mobile-container#battery-section::after{
  content: "";
  position: absolute;
  width: 250px;
  height: 250px;
  background: linear-gradient(#ff2371,rgba(255,40,113,0.2));
  border-radius: 50%;
  transform: translate(150px, 150px);
  }
.battery-box{
  position: relative;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.1);
  z-index: 1;
  box-shadow: 0 25px 45px rgba(0,0,0,0.25);
  border: 1px solid rgba(255,255,255,0.25);
  border-right: 1px solid rgba(255,255,255,0.1);
  border-bottom: 1px solid rgba(255,255,255,0.1);
  -webkit-backdrop-filter: blur(25px);

  backdrop-filter: blur(25px);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  .battery-content{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    h3{
      font-family: "Poppins", sans-serif;
      color: #9d2cb1;
      font-weight: 600;
      font-size: 1.4em;
      letter-spacing: 1px;
      }
    .battery-shape{
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 140px;
      height: 65px;
      margin: 20px 0;
      border: 3px solid #111;
      border-radius: 10px;
      &::before{
        content: "";
        position: absolute;
        top: 50%;
        right: -12px;
        transform: translateY(-50%);
        width: 7px;
        height: 16px;
        background: #111;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        }
      &::after{
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 50%;
        background: rgba(255,255,255,0.1);
        }
      .battery-level{
        position: absolute;
        top: 4px;
        left: 4px;
        right: 4px;
        bottom: 4px;
        background: #111;
        border-radius: 4px;
        overflow: hidden;
        .battery-percentage{
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          width: 50%;
          background: linear-gradient(90deg, #9c28af, #fd2c72);
          border-radius: 4px;


          }
        }
      }
    .battery-percent{
      font-family: "Poppins", sans-serif;
      color: #fff;
      font-size: 2.5em;
      z-index: 3;
      }
    }

  }

#battery{
  width: 250px;
  height: 90px;
  border: 1px solid #fff;
  border-radius: 6px;
  padding: 10px;
  position: relative;
  &::after{
    content: "";
    display: block;
    width: 15px;
    height: 40px;
    position: absolute;
    background: transparent;
    margin-top: -20px;
    top: 50%;
    right: -17px;
    border: 1px solid #fff;
    border-top-right-radius: 4px;
    border-left-color: #000;
    border-bottom-right-radius: 4px;
    }
  #battery-level{
    position: relative;
    height: 100%;
    width: 30%;
    background: linear-gradient(90deg, #9c28af, #fd2c72);
    transition: all 1s ease;
    &::after{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 50%;
      background: rgba(255,255,255,0.1);
      }
    }
  #battery-level-digit{
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    text-align: center;
    font-size: 2.5em;
    line-height: 100px;
    color: #fafafa;
    }
  }
#battery-level.battery-animation{
  animation-name: battery-charget;
  animation-duration: 4s;
  animation-iteration-count: infinite;
  }
@keyframes battery-charget {
  0%{
    width: 20%;
    }
  100%{
    width: 100%;
    }
  }