:root {
  --color-basic: #09131b;
}

@media only screen and (max-width: 400px) {
  @-ms-viewport {
    width: 320px;
  }
}

:root {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  height: 100vh;
}

body {
  background: var(--color-basic);
  color: #427dad;
}

.page {
  overflow: hidden;
  width: 100%;
}

ul,
li {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

section {
  margin-bottom: 10px;
  padding: 2px;
}

.mobile-container {
  width: 320px;
  padding: 0 12px;
  background: #000;
  margin: 0 auto;
}

.pc-container {
  width: 1200px;
  padding: 20px;
  margin: 0 auto;
}

.mobile-container {
  margin-bottom: 10px;
  padding: 8px;
}

.mobile-container .skills-img {
  width: 100%;
}

.mobile-container .skills-heading {
  text-transform: uppercase;
  text-align: center;
}

.mobile-container .logo {
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.2;
  padding: 10px;
  font-size: 36px;
  letter-spacing: 4px;
  font-weight: 900;
  text-transform: uppercase;
  flex-direction: column;
}

.mobile-container .logo span {
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.2;
}

.mobile-container .menu {
  margin-bottom: 24px;
}

.mobile-container .menu ul {
  padding: 6px 0;
}

.mobile-container .menu ul li {
  text-align: left;
  list-style: none;
  margin-bottom: 2px;
  border-left: 1px solid #1a3246;
}

.mobile-container .menu ul li:last-child {
  margin-bottom: 0;
}

.mobile-container .menu ul li img {
  height: 70%;
  object-fit: contain;
  margin-right: 20px;
}

.mobile-container .menu ul li a {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  width: 100%;
  background: #122331;
  text-transform: uppercase;
  font-weight: 600;
}

.mobile-container .menu ul li a:hover {
  background: #363636;
}

.mobile-container .menu ul li a:active {
  background: #555;
}

.wrapper {
  height: 100vh;
}

.progressbar {
  z-index: 2;
  position: fixed;
  height: 10px;
  width: 100%;
  background: #56CCF2;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2F80ED, #56CCF2);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #2F80ED, #56CCF2);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.progressbar .progress-line {
  height: 10px;
  width: 0;
  background: #0575E6;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #021B79, #0575E6);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #021B79, #0575E6);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.change-color-theme li {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  list-style: none;
  padding: 5px 0;
}

.change-color-theme li a {
  height: 20px;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
  color: #fff;
  line-height: 1.3;
  border: 1px solid #333;
  width: 150px;
  padding: 8px 0;
  text-transform: uppercase;
}

.change-color-theme li a span.material-icons {
  margin-left: 20px;
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 16px;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: "liga";
}

.white body {
  background: #fafafa;
}

.white body a {
  color: #09131b;
}

.white body h2 {
  color: #09131b;
}

.white body p {
  color: #09131b;
}

.white body .menu a {
  color: #fafafa;
}

.white body .logo {
  color: #000000;
}

.white body .mobile-container {
  background: #d0c6c6;
}

#battery-section {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.mobile-container#battery-section {
  background: none;
}

.mobile-container#battery-section::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 240px;
  background: linear-gradient(#f3f5f5, #9c27b0);
  border-radius: 50%;
  transform: translate(-150px, -100px);
}

.mobile-container#battery-section {
  height: 450px;
}

.mobile-container#battery-section::after {
  content: "";
  position: absolute;
  width: 250px;
  height: 250px;
  background: linear-gradient(#ff2371, rgba(255, 40, 113, 0.2));
  border-radius: 50%;
  transform: translate(150px, 150px);
}

.battery-box {
  position: relative;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.1);
  z-index: 1;
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.25);
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-right: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  -webkit-backdrop-filter: blur(25px);
  backdrop-filter: blur(25px);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.battery-box .battery-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.battery-box .battery-content h3 {
  font-family: "Poppins", sans-serif;
  color: #9d2cb1;
  font-weight: 600;
  font-size: 1.4em;
  letter-spacing: 1px;
}

.battery-box .battery-content .battery-shape {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 140px;
  height: 65px;
  margin: 20px 0;
  border: 3px solid #111;
  border-radius: 10px;
}

.battery-box .battery-content .battery-shape::before {
  content: "";
  position: absolute;
  top: 50%;
  right: -12px;
  transform: translateY(-50%);
  width: 7px;
  height: 16px;
  background: #111;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.battery-box .battery-content .battery-shape::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: rgba(255, 255, 255, 0.1);
}

.battery-box .battery-content .battery-shape .battery-level {
  position: absolute;
  top: 4px;
  left: 4px;
  right: 4px;
  bottom: 4px;
  background: #111;
  border-radius: 4px;
  overflow: hidden;
}

.battery-box .battery-content .battery-shape .battery-level .battery-percentage {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 50%;
  background: linear-gradient(90deg, #9c28af, #fd2c72);
  border-radius: 4px;
}

.battery-box .battery-content .battery-percent {
  font-family: "Poppins", sans-serif;
  color: #fff;
  font-size: 2.5em;
  z-index: 3;
}

#battery {
  width: 250px;
  height: 90px;
  border: 1px solid #fff;
  border-radius: 6px;
  padding: 10px;
  position: relative;
}

#battery::after {
  content: "";
  display: block;
  width: 15px;
  height: 40px;
  position: absolute;
  background: transparent;
  margin-top: -20px;
  top: 50%;
  right: -17px;
  border: 1px solid #fff;
  border-top-right-radius: 4px;
  border-left-color: #000;
  border-bottom-right-radius: 4px;
}

#battery #battery-level {
  position: relative;
  height: 100%;
  width: 30%;
  background: linear-gradient(90deg, #9c28af, #fd2c72);
  transition: all 1s ease;
}

#battery #battery-level::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background: rgba(255, 255, 255, 0.1);
}

#battery #battery-level-digit {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  text-align: center;
  font-size: 2.5em;
  line-height: 100px;
  color: #fafafa;
}

#battery-level.battery-animation {
  animation-name: battery-charget;
  animation-duration: 4s;
  animation-iteration-count: infinite;
}

@keyframes battery-charget {
  0% {
    width: 20%;
  }
  100% {
    width: 100%;
  }
}
